.menu-container {
    ul {
        list-style: none;
        display: flex;

        li a {
            display: block;
            text-decoration: none;
            padding: 20px;
            color: white;
            width: 100px;
            background: #546e7a;

            &:hover {
                background: #29434e;
            }
        }
    }
}