.plans-container {
    .content {
        padding: 0 40px;

        .classes {
            padding: 2px 4px;
            margin: 20px 0;
        }
    }
    .plan {
        display: flex;

        .col {
            border: 1px solid #9e9e9e;
            margin: 0 4px;
            padding: 10px;

            .sub-name {
                font-size: 16px;
                font-weight: bold;
            }

            .sub {
                margin: 10px 0;

                span {
                    color: #424242;
                }

                select { 
                    padding: 2px 4px;
                }
            }
        }
    }
}