.login-container {
    display: flex;
    justify-content: center;

    .form {
        display: flex;
        flex-direction: column;
        width: 400px;
        background: #424242;
        padding: 20px;

        input {
            border: 1px solid black;
            padding: 6px 10px;
            margin-bottom: 20px;
            outline: none;
        }
        
        button {
            border: none;
            background: #b91400;
            color: white;
            padding: 6px 10px;
            cursor: pointer;
        }
    }
}